.recetas-container {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows:1fr 1fr 1fr;
  grid-template-columns:repeat(
    auto-fill,
    minmax(300px, 1fr)
  
  );
  padding: 30px;
  grid-gap: 30px;
  background-color: azure;
}

  .recetas{
    background-color: azure;
